@import url(https://fonts.googleapis.com/css2?family=Mirza:wght@600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=El+Messiri:wght@600&display=swap);
body{margin:0;padding:0;background:linear-gradient(to right, #f0c, #339);font-size:17px;font-family:"Mirza", cursive;direction:rtl}

.navbar,footer{height:5rem;background-color:#fff;box-shadow:0 4px 8px 0 rgba(0,0,0,0.2)}.navbar .vendor-left,footer .pow-left{float:left;margin-left:25px;margin-top:24px}.navbar .meg-center{text-align:center;padding-top:20px}.header-title{color:#fff;font-size:clamp(1.5rem, 1.6vw, 2.5rem);margin-right:20px}.container{display:grid;grid-template-columns:repeat(3, 1fr);justify-items:center}.container .list-wrapper{display:contents}.container .list-wrapper .card{box-shadow:0 4px 8px 0 rgba(0,0,0,0.2);width:95%;height:35rem;margin:15px;border-radius:5px;background-color:#fff}.container .list-wrapper .card .card_img{height:100%;display:contents}.container .list-wrapper .card .card_img img{width:95%;height:70%;border-radius:5px;display:block;margin-left:auto;margin-right:auto;margin-top:15px}.container .list-wrapper .card h2{color:#0b58e6;font-size:clamp(1.1rem, 1.6vw, 2rem);text-align:center;margin:25px 40px 15px 40px}.container .list-wrapper .card .center{text-align:center}.container .list-wrapper .card .center button{background:#d60ac2;border-radius:5px;border:none;padding:10px 20px;width:50%;height:11%;color:#fff;text-align:center;text-decoration:none;display:inline-block;font-size:clamp(1rem, 1.2vw, 2.7rem);overflow:hidden;cursor:pointer;font-family:"El Messiri", sans-serif}footer .icon img{padding:20px}@media screen and (max-width: 992px){.container{grid-template-columns:repeat(2, 1fr)}}

